import Vue from 'vue'
import Router from 'vue-router'
 

Vue.use(Router)

export default new Router({
 
  routes: [
    {
      path: '/',
      name: '首页',
      component: resolve => require(['../pages/Home.vue'], resolve),
      // redirect: '/Home',
      meta: {
        auth: true
      },
    }, //首页
    // {
    //   path: '/Home',
    //   name: '首页',
    //   component: resolve => require(['../pages/Home.vue'], resolve),
    // }, //首页
    {
      path: '/Practice',
      name: '练习',
      component: resolve => require(['../pages/Practice.vue'], resolve),
      meta: {
        auth: true
      }
    }, //练习
    {
      path: '/Data',
      name: '数据',
      component: resolve => require(['../pages/Data.vue'], resolve),
      meta: {
        auth: true
      }
    }, //数据
    {
      path: '/Aboutme',
      name: '关于我',
      component: resolve => require(['../pages/Aboutme.vue'], resolve),
      meta: {
        auth: true
      }
    }, //关于
    
    {
      path: '/Aboutwebsite',
      name: '本站',
      component: resolve => require(['../pages/Aboutwebsite.vue'], resolve),
      meta: {
        auth: true
      }
    }, //本站
    
  ]
})



